


import React from 'react';
import { withRouter } from 'react-router-dom'

import { Layout } from 'element-react';

import '@/styles/footer.css';


class footer extends React.Component {
    constructor(props) {
        super(props);

        this.state = {
        };
    }
    render() {

        let phone_icon = require("@/assets/veritas/phone.png")
        let email_icon = require("@/assets/veritas/email.png")
        let global_icon = require("@/assets/veritas/global.png")
        let weixin_url_icon = require("@/assets/veritas/weixin_url.png")

        return (
            <div>
                {/*  */}
                <div className="footer">
                    <Layout.Row gutter="0" className="content">
                        <Layout.Col lg="3" className="footerC">&nbsp;</Layout.Col>
                        <Layout.Col lg="5" className="footerC"><span className="footerC"><span><img className="footer_img" src={phone_icon} alt="" /></span><span className="footer_text">&nbsp;联系电话：400-120-4816</span></span></Layout.Col>
                        <Layout.Col lg="5" className="footerC"><span className="footerC"><span><img className="footer_img" src={email_icon} alt="" /></span><span className="footer_text">&nbsp;邮箱：veritas.china@veritas.com</span></span></Layout.Col>
                        <Layout.Col lg="5" className="footerC"><span className="footerC"><span><img className="footer_img" src={global_icon} alt="" /></span><span className="footer_text">&nbsp;官网：<span className="veritas_url">www.veritas.com/zh/cn</span></span></span></Layout.Col>
                        <Layout.Col lg="3" className="footerC footerD"><span className="footerE"><img className="footer_img2" src={weixin_url_icon} alt="" /><p class="wxgzh">微信公众号</p></span></Layout.Col>
                        <Layout.Col lg="3" className="footerC">&nbsp;</Layout.Col>
                    </Layout.Row>
                    <br />
                    <p className="vertas_right">©1995-2019 年 Veritas Technologies LLC 版权所有</p>
                </div>

                {/*  */}
            </div>
        )
    }

}

export default withRouter(footer)
