


import React from 'react';
import { withRouter } from 'react-router-dom'

import { Layout } from 'element-react';
// import { Button } from 'element-react';  // 引入element-react

import '@/styles/header.css';


class header extends React.Component {
    constructor(props) {
        super(props);

        this.state = {
        };
        this.goUrl = this.goUrl.bind(this);

    }
    goUrl(num) {
        let url;

        if (num === 1) {
            url = "https://icampaign.com.cn/Veritas/index.php?m=VeritasWebinar&channel=DB"
        } else if (num === 2) {
            url = "https://icampaign.com.cn/Veritas/index.php?m=VeritasWebinar&a=pdf&channel=DB"
        } else if (num === 3) {
            url = ""
        } else if (num === 4) {
            url = "https://www.veritas.com/zh/cn"
        } else if (num === 5) {
            url = ""
        } else if (num === 6) {
            url = "https://icampaign.com.cn/Veritas/index.php?m=VeritasWebinar&a=reg&channel=DB"
        }
        
        window.location.href = url
    }
    render() {

        let logo_icon = require("@/assets/veritas/logo.png")
        let home_icon = require("@/assets/veritas/home.png")
        let weixin_icon = require("@/assets/veritas/weixin.png")

        return (
            <div className="header">
                {/*  */}
                <Layout.Row gutter="0">
                    <Layout.Col lg="2">&nbsp;</Layout.Col>
                    <Layout.Col lg="3" className="left_logo"><img className="logo_icon" src={logo_icon} alt="" /></Layout.Col>
                    <Layout.Col lg="2"><span onClick={() => this.goUrl(1)} className="header_title">首页大厅</span></Layout.Col>
                    <Layout.Col lg="2"><span onClick={() => this.goUrl(2)} className="header_title">资料库</span></Layout.Col>
                    <Layout.Col lg="2"><span onClick={() => this.goUrl(3)} className="header_title">在线提问</span></Layout.Col>
                    <Layout.Col lg="3"><span onClick={() => this.goUrl(4)} className="header_title"><span className="mypic"><img className="home_icon" src={home_icon} alt="" /></span><span className="title_details">进入官网</span></span></Layout.Col>
                    <Layout.Col lg="3"><span onClick={() => this.goUrl(5)} className="header_title"><span className="mypic"><img className="weixin_icon" src={weixin_icon} alt="" /></span><span className="title_details">官方微信</span></span></Layout.Col>
                    <Layout.Col lg="2">&nbsp;</Layout.Col>
                    <Layout.Col lg="3"><span onClick={() => this.goUrl(6)} className="header_title header_title2">登录&nbsp;丨&nbsp;注册</span></Layout.Col>
                    <Layout.Col lg="2">&nbsp;</Layout.Col>
                </Layout.Row>
                {/*  */}
            </div>
        )
    }
}

export default withRouter(header)
